.txt2{border: 1px solid blue; color: green;}

@-webkit-keyframes span{
    0%{-webkit-transform: rotate(0deg);}
    100%{-webkit-transform: rotate(360deg);}
}
@keyframes span{
    0%{-webkit-transform: rotate(0deg);}
    100%{-webkit-transform: rotate(360deg);}
}

.loader{
    width: 120px;
    height: 120px;
    border: 16px solid #eaeaea;
    border-radius: 50%;
    border-top-color: orange;
    font-size: 0;
    -webkit-animaion: span 2s linear infinite;
    animation: span 2s linear infinite;
    cursor: pointer;
}
.loader:hover{
    animation-play-state: paused;
}